import { TutorialSection } from "./TutorialStage";
import { ArrowLeft16 } from "@carbon/icons-react";

const PageSteps = ({
  steps,
  preview,
  initiallyOpen,
  handleLinkClick,
  name,
}) => {
  // const history = useHistory();

  const result = [];
  const [openSection, setOpenSection] = useState(
    preview ? "Overview" : initiallyOpen ? initiallyOpen : "Create"
  );
  const handleHeadingClick = (name) => {
    setOpenSection(name);
  };

  // Preview is always TRUE for now as this only covers Overview
  if (preview) {
    result.push(
      <li
        className="bx--accordion__item bx--accordion__item--active tutorial-stage tutorial-stage--preview"
        key="Overview-preview"
      >
        {
          /*
          <button
            aria-controls="accordion-item-1"
            aria-expanded="true"
            className="bx--accordion__heading"
            title="Expand/Collapse"
            type="button"
          >
            <div className="bx--accordion__title">
              Overview
            </div>
          </button>
          */
        }
        <div className="bx--accordion__title tutorial-stage--preview__title">
          Overview
        </div>
        <div id="accordion-item-1" className="bx--accordion__content">
          <TutorialPreview name={name} />
        </div>
      </li>
    );
    result.push(
      <li className="bx--accordion__item tutorial-stage" key="tutorial-start">
        <Button
          className="tutorial-start"
          onClick={() => handleLinkClick(name)}
        >
          Start
        </Button>
      </li>
    );
  }
  /*
  else {
    result.push(
      <AccordionItem
        title="Overview"
        open={"Overview" === openSection}
        className="tutorial-stage"
        onHeadingClick={() => handleHeadingClick("Overview")}
        key="Overview"
      >
        <TutorialPreview />
      </AccordionItem>
    );

    for (const index in steps) {
      const step = steps[index];
      result.push(
        <TutorialSection
          key={step.overview.name}
          open={step.overview.name === openSection}
          handleClick={(name) => handleHeadingClick(name)}
          stage={step}
        />
      );
    }
  }
  */
  return result;
};

export const Tutorial = ({ name, preview, initiallyOpen, handleLinkClick }) => {
  const tutorialBaseClass = "tutorial";
  // const history = useHistory();
  const [tutorialClasses, setTutorialClasses] = useState("");
  console.log(`Tutorial path ${name} selected`);

  const pageSteps = aTutorial.tutorial.pageSteps;

  useEffect(() => {
    if (preview) {
      setTutorialClasses(`${tutorialBaseClass} ${tutorialBaseClass}--preview`);
    } else {
      setTutorialClasses(`${tutorialBaseClass}`);
    }
  });

  return (
    <div className={tutorialClasses}>
      {
      /*
      <header className="tutorial__header">
        <Breadcrumb className="tutorial__breadcrumb" noTrailingSlash>
          {!preview && (
            <BreadcrumbItem
              isCurrentPage={preview}
              className="tutorial__breadcrumb--tutorials"
            >
              <a
                href="#"
                onClick={(ev) => {
                  ev.preventDefault();
                  return history.goBack();
                }}
              >
                <ArrowLeft16
                  className="tutorial__breadcrumb__left-arrow"
                  aria-label="Back arrow indicator"
                />
                Tutorials
              </a>
            </BreadcrumbItem>
          )}
          <BreadcrumbItem isCurrentPage>
            <span>{name}</span>
          </BreadcrumbItem>
        </Breadcrumb>
      </header>
      */
      }
      <main className="tutorial__content">

        <Accordion>
          <PageSteps
            steps={pageSteps}
            preview={preview}
            initiallyOpen={initiallyOpen}
            name={name}
            handleLinkClick={handleLinkClick}
          />
        </Accordion>
      </main>
    </div>
  );
};
