export const withHeadingRenderFunction = {
  headingRenderFunction: (headingJSX) => (
    <React.Fragment>
      <Education32 {...idAttribute("custom-header-content")} />
      {headingJSX}
    </React.Fragment>
  ),
};

const tutorials = repoMetadata.categories[0].tutorials.slice();
const TutorialsListItems = ({ search, sort, handleClick }) => {
  const filterRegex = new RegExp(search, "i");
  const filterFunc = (item) =>
    filterRegex.exec(item.name) !== null ||
    filterRegex.exec(item.tags) !== null ||
    filterRegex.exec(item.category) !== null;
  // const sortFunc = (a, b) => a.name.localeCompare(b.name);

  const list = tutorials.filter(filterFunc); // .sort(sortFunc);

  const listDom = [];
  for (const item of list) {
    const _tags = (item.tags && item.tags.split(";")) || [];
    const _categories = (item.category && item.category.split(",")) || [];
    listDom.push(
      <ListPanelItem
        name={item.name}
        desc={item.shortDesc}
        durationMins={item.durationMins}
        detailsLink={item.detailsURL}
        tags={_tags.concat(_categories)}
        key={item.name}
        handleLinkClick={(preview) => handleClick(item.name, preview)}
        hasButtons={false}
      />
    );
  }
  return listDom;
};

const PromotedTutorial = ({ name, image, durationMins, handleClick }) => {
  const imgSource = process.env.PUBLIC_URL + '/static_files/' + image + '.svg'
  return (
    <div className="promoted-tutorial__container bx--col-sm-1 bx--col-md-2 bx--col-lg-4 bx--col-xlg-4 bx--col-max-4">
      <ClickableTile
        handleClick={() => handleClick(name)}
        className="promoted-tutorial__tile"
        tabIndex="0"
      >
        <p className="promoted-tutorial__text">{name}</p>
        <div className="promoted-tutorial__lower">
          <img src={imgSource} className="promoted-tutorial__img" alt="Tutorial image" />
          <div className="promoted-tutorial__duration">
            <div className="promoted-tutorial__lower-left">
              <Time20
                className="promoted-tutorial__duration-img"
                aria-label="Timing indicator"
              />
              <span className="promoted-tutorial__duration-text">
                {durationMins} minutes
              </span>
            </div>
            <ArrowRight20
              className="promoted-tutorial__arrow-right"
              aria-label="Arrow indicating moving on"
            />
          </div>
        </div>
      </ClickableTile>
    </div>
  );
};

const PromotedTutorials = ({ handleClick }) => {
  // const history = useHistory();

  const list = [
    {
      name: "Creating a very simple message flow",
      image: 'message-flow',
      durationMins: "5",
    },
    {
      name: "Creating an Integration Server",
      image: 'integration-server',
      durationMins: "5",
    },
    {
      name: "Using a REST API to manage a set of records",
      image: 'rest-api',
      durationMins: "15",
    },
  ];

  const listDom = [];
  for (const item of list) {
    listDom.push(
      <PromotedTutorial
        name={item.name}
        image={item.image}
        key={item.name}
        className="promoted-tutorial"
        handleClick={handleClick}
        durationMins={item.durationMins}
      />
    );
  }
  return listDom;
};

const filterOptions = [
  {
    label: "Tags",
    options: [],
  },
];

const initFilterOptions = () => {
  const allTagOptions = [];
  const types = [
    "red",
    "magenta",
    "purple",
    "blue",
    "cyan",
    "teal",
    "green",
    "gray",
    "cool-gray",
    "warm-gray",
  ];
  const allTags = new Set();
  for (const item of tutorials) {
    const tags = (item.tags && item.tags.split(";")) || [];
    const categories = (item.category && item.category.split(",")) || [];

    for (const tag of tags) {
      allTags.add(tag);
    }
    for (const cat of categories) {
      allTags.add(cat);
    }
  }

  for (const tag of allTags) {
    allTagOptions.push({
      type: types[Math.floor(Math.random() * types.length)],
      value: tag,
      label: tag,
    });
  }

  filterOptions.push({
    label: "Tags",
    options: allTagOptions,
  });
};
initFilterOptions();

export const TutorialsPage = (route) => {
  const history = useHistory();
  const [search, setSearch] = useState("");
  const [panelOpen, setPanelOpen] = useState(false);
  const [tutorialDetails, setTutorialDetails] = useState({});

  const handleLinkClick = (tutorialName) => {
    console.log(`Link click for "${tutorialName}"`);
    history.push(`/tutorial/${tutorialName}${route.location.search}`);
  };

  const handleTutorialClick = (tutorialName, preview) => {
    console.log(
      `Details active for "${tutorialName}"`,
      preview,
      route.location.search.toString()
    );

    if (route.location.search === "?option=green" || preview) {
      setPanelOpen(true);
      setTutorialDetails({ title: tutorialName });
    } else {
      history.push(`/tutorial/${tutorialName}${route.location.search}`);
    }
  };

  const handlePromotedClick = (tutorialName) => {
    if (
      route.location.search === "?option=green" ||
      route.location.search !== "?option=yellow"
    ) {
      setPanelOpen(true);
      setTutorialDetails({ title: tutorialName });
    } else {
      history.push(`/tutorial/${tutorialName}${route.location.search}`);
    }
  };

  const handleClose = () => {
    setPanelOpen(false);
  };

  const handleFilterChange = (val) => {
    if (val) {
      const valMap = val.map((item) => item.value);
      setSearch(`((${valMap.join(")|(")}))`);
    } else {
      setSearch("");
    }
  };

  return (
    // <LayoutGrid
    //   main={
    <LayoutNav>
      <IdePageHeader heading="Tutorials" {...withHeadingRenderFunction} />
      <IdePageContent>
        <div className="tutorials">
          {/* <Search
            className="tutorials__serach"
            labelText=""
            placeHolderText="What do you want to learn today?"
            onInput={(event) => setSearch(event.target.value)}
            value={search}
          /> */}
          <IdeFilter
            options={filterOptions}
            onChange={handleFilterChange}
            placeholderText="What are you looking for today?"
            searchForText="Search for"
          />
          <div className="tutorials-page__section">
            {search.length === 0 && (
              <React.Fragment>
                {/* <h3 className="tutorials-page__sub-heading">Getting started</h3> */}
                <ul className="tutorials__promoted-list">
                  <PromotedTutorials handleClick={handlePromotedClick} />
                </ul>
              </React.Fragment>
            )}
            {/* <h3 className="tutorials-page__sub-heading tutorials-page__sub-heading--short">
              All tutorials
            </h3> */}
            <ul className="tutorials__list">
              {
                /*
                <TutorialsListItems
                  search={search}
                  handleClick={handleTutorialClick}
                />
                */
              }
              <TutorialsListItems
                search={search}
                handleClick={handlePromotedClick}
              />
            </ul>
          </div>
        </div>
      </IdePageContent>
      <IdeSlideOverPanel
        open={panelOpen}
        onOverlayClose={handleClose}
        onClose={handleClose}
        size="medium"
        title={tutorialDetails.title}
      >
        <Tutorial
          name={tutorialDetails.title}
          preview={true}
          handleLinkClick={() => handleLinkClick(tutorialDetails.title)}
        />
      </IdeSlideOverPanel>
    </LayoutNav>
    //   }
    // />
  );
};
