import React from "react";
import {
  Header,
  HeaderName,
  HeaderMenuItem,
  HeaderNavigation,
} from "carbon-components-react";
import { IdeNavigation, IdeSideNavLink } from "@hybrid-cloud/ide-components";

import { ROUTER_CONFIG } from "../assets/Navigation.config.js";
import { NavLink } from "react-router-dom";
import "./LayoutNav.scss";

const ariaAttributes = {
  "aria-labelledby": "#myId",
  "aria-label": "sample aria label to describe the nav",
};

export const LayoutNav = ({ children }) => (
  <React.Fragment>
    <Header aria-label="IBM Toolkit">
      <HeaderName href="#">
        App Connect&nbsp;<span className="header-highlight">Toolkit</span>
      </HeaderName>
      <HeaderNavigation aria-label="IBM Toolkit">
        <HeaderMenuItem>Close tutorials</HeaderMenuItem>
      </HeaderNavigation>
    </Header>
    <IdeNavigation
      ariaAttributes={ariaAttributes}
      navigationLinks={ROUTER_CONFIG.links.map((linkToGen, index) => (
        <IdeSideNavLink
          key={`link-${index}-top-level`}
          renderIcon={linkToGen.icon}
          element={NavLink}
          to={{ pathname: linkToGen.path }}
        >
          {linkToGen.label}
        </IdeSideNavLink>
      ))}
    >
      <div className="main-content carbon-theme--g10">
        {children}
      </div>
    </IdeNavigation>
  </React.Fragment>
);
